import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormBuilder, FormGroup, FormsModule, ReactiveFormsModule, Validators } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { MenuService } from 'src/app/services/menu-service.service';
import { SuccessPartyModelComponent } from '../success-party-model/success-party-model.component';
import { ApiService } from 'src/app/services/apiService';

@Component({
  selector: 'send-details-block',
  standalone: true,
  imports: [CommonModule,FormsModule, ReactiveFormsModule, SuccessPartyModelComponent],
  providers: [ApiService],
  templateUrl: './send-details-block.component.html',
  styleUrls: ['./send-details-block.component.scss']
})
export class SendDetailsBlockComponent implements OnInit {

  public userDataForm!: FormGroup; // user data form
  public submitted: boolean = false;
  public isReady :boolean = false;
  @Output() close = new EventEmitter<boolean>();
  constructor(private formBuilder: FormBuilder,public menuService:MenuService) {}

  ngOnInit(): void {
    this.loadForm();
  }

  loadForm() {
    this.userDataForm = this.formBuilder.group({
      email: [null, [Validators.required, Validators.email]],
      userName: [null, [Validators.required, Validators.minLength(3)]],
      phone: [null, [Validators.required, Validators.minLength(10)]],
      message: [null, Validators.required],
    });
  }

  get userDataFormControls() {
    return this.userDataForm.controls;
  }

  sendUserData() {
    this.submitted = true;
    if (this.userDataForm.invalid) {
      return;
    }
    let updateData = {
      email: this.userDataForm.value.email,
      firstName: this.userDataForm.value.userName,
      phone: this.userDataForm.value.phone,
      message: this.userDataForm.value.message,
    };

        this.isReady = true;
        setTimeout(()=> {
          this.isReady= false;
          this.userDataForm.reset();
          this.submitted = false;
          this.close.emit(false);
          this.menuService.contactDetailsSubmitted= true;
        },2000)
    console.log(updateData, 'userEditForm.value');
  }

}
